import groupBy from 'lodash/groupBy';
import reduce from 'lodash/reduce';
import { _ } from '@splunk/ui-utils/i18n';
import AddChartDropdown from '@splunk/dashboard-icons/AddChartDropdown';
import { vizCategories } from './utils/presetUtils';

/**
 * Helper method to filter the category list
 * @method filterByCategory
 * @param {Array} categories List of categories
 * @param {String} category Portion of category name to search for
 * @returns Array
 * @private
 */
const filterByCategory = (categories, category) => {
    return categories.filter(({ category: cat }) =>
        new RegExp(category, 'i').test(cat)
    );
};

/**
 * Helper method to filter items from category lists
 * @method filterByLabel
 * @param {Array} categories List of categories
 * @param {String} label Portion of viz label to search for
 * @returns Array
 */
const filterByLabel = (categories, label) => {
    const filteredCategories = [];

    categories.forEach(({ category: cat, visualizations }) => {
        const list = visualizations.filter(({ label: l }) =>
            new RegExp(label, 'i').test(l)
        );

        if (list.length) {
            filteredCategories.push({ category: cat, visualizations: list });
        }
    });

    return filteredCategories;
};

/**
 * Maps visualization metadata to a taxonomy
 * @class VisualizationFilter
 */
class VisualizationFilter {
    /**
     * @constructor
     * @param {Object} preset Hash of dashboard presets
     * @param {Array} [taxonomy] List of category -> visualizationId mapping [{ category: Foo, visualizations: ['viz.foo'] }]
     */
    constructor(preset) {
        if (!preset || !preset.visualizations) {
            throw new Error('Must provide a valid preset');
        }

        const categoryValues = [...vizCategories.values()];

        this.taxonomy = Object.entries(
            groupBy(
                Object.values(preset.visualizations).map(
                    ({
                        config: { category, key, icon, name } = {},
                        meta,
                        includeInVizSwitcher,
                        includeInToolbar,
                    }) => ({
                        type: key,
                        label: name,
                        icon: icon ?? AddChartDropdown,
                        initialOptions: meta?.initialOptions ?? {},
                        category,
                        includeInVizSwitcher,
                        includeInToolbar,
                    })
                ),
                'category'
            )
        )
            .map(([category, visualizations]) => ({
                category,
                visualizations,
            }))
            // sorting by category index
            .sort(
                (catA, catB) =>
                    categoryValues.indexOf(catA.category) -
                    categoryValues.indexOf(catB.category)
            );
    }

    /**
     * Retrieve a filtered taxonomy of visualization metadata
     * @method getVisualizationList
     * @param {Object} [searchCriteria] Filter { label, category}
     * @returns Array of category metadata
     */
    getVisualizationList({ label, category } = {}) {
        let filteredViz = [];
        let filteredVizByLabel = [];
        let filteredVizByCategory = [];

        if (category) {
            filteredVizByCategory = filterByCategory(this.taxonomy, category);
        }

        if (label) {
            filteredVizByLabel = filterByLabel(this.taxonomy, label);
        }

        if (!label && !category) {
            filteredViz = this.taxonomy;
        } else if (filteredVizByCategory.length && filteredVizByLabel.length) {
            const categoriesList = this.taxonomy.map(
                ({ category: cat }) => cat
            );
            filteredVizByCategory = reduce(
                filteredVizByCategory,
                (result, item) => {
                    result[item.category] = item.visualizations; // eslint-disable-line no-param-reassign
                    return result;
                },
                {}
            );

            filteredVizByLabel = reduce(
                filteredVizByLabel,
                (result, item) => {
                    if (!filteredVizByCategory[item.category]) {
                        result[item.category] = item.visualizations; // eslint-disable-line no-param-reassign
                    }
                    return result;
                },
                {}
            );

            filteredViz = reduce(
                categoriesList,
                (result, cat) => {
                    if (filteredVizByCategory[cat] || filteredVizByLabel[cat]) {
                        result.push({
                            category: _(cat),
                            visualizations:
                                filteredVizByCategory[cat] ||
                                filteredVizByLabel[cat],
                        });
                    }
                    return result;
                },
                []
            );
        } else if (filteredVizByCategory.length) {
            filteredViz = filteredVizByCategory;
        } else if (filteredVizByLabel.length) {
            filteredViz = filteredVizByLabel;
        }
        return filteredViz;
    }
}

export default VisualizationFilter;
